/* Flash Cards Game - Ultra Modern Platform-Native Design */
.flash-cards-container {
    background: linear-gradient(135deg, #0f0c29, #302b63, #24243e);
    color: white;
    font-family: 'Arial', sans-serif;
    min-height: 100vh;
    padding: 20px;
    position: relative;
    overflow-x: hidden;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

/* Premium animated background sparkles */
.flash-cards-container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: 
        radial-gradient(2px 2px at 20px 30px, rgba(0, 224, 255, 0.4), transparent),
        radial-gradient(2px 2px at 40px 70px, rgba(0, 224, 255, 0.3), transparent),
        radial-gradient(1px 1px at 90px 40px, rgba(255, 255, 255, 0.15), transparent),
        radial-gradient(1px 1px at 130px 80px, rgba(0, 224, 255, 0.2), transparent);
    background-repeat: repeat;
    background-size: 200px 200px;
    animation: sparkle 20s linear infinite;
    pointer-events: none;
    z-index: 0;
}

@keyframes sparkle {
    0% { transform: translateX(0); }
    100% { transform: translateX(200px); }
}

.flash-cards-container > * {
    position: relative;
    z-index: 1;
}

.flash-cards-loading {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    color: white;
    font-size: 1.5rem;
}

/* Ultra premium header */
.flash-cards-header {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    align-items: center;
    margin-bottom: 40px;
    padding: 30px 40px;
    background: linear-gradient(135deg, rgba(15, 12, 41, 0.95), rgba(27, 20, 68, 0.9));
    border-radius: 30px;
    backdrop-filter: blur(35px);
    border: 1px solid rgba(0, 224, 255, 0.3);
    box-shadow: 
        0 25px 50px rgba(0, 0, 0, 0.4),
        inset 0 1px 0 rgba(255, 255, 255, 0.15);
    position: relative;
    overflow: hidden;
    gap: 20px;
    min-height: 120px;
}

.flash-cards-header::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, #00e0ff, #4facfe, #0099cc);
    border-radius: 30px 30px 0 0;
    box-shadow: 0 0 25px rgba(0, 224, 255, 0.6);
}

.flash-cards-header::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 20%;
    width: 150px;
    height: 150px;
    background: radial-gradient(circle, rgba(0, 224, 255, 0.1) 0%, transparent 70%);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    animation: headerGlow 10s ease-in-out infinite;
}

@keyframes headerGlow {
    0%, 100% { transform: translate(-50%, -50%) scale(1); opacity: 0.3; }
    50% { transform: translate(-50%, -50%) scale(1.3); opacity: 0.6; }
}

.flash-cards-header h1 {
    color: white;
    font-size: 2.2rem;
    font-weight: bold;
    margin: 0;
    background: linear-gradient(45deg, #ffffff, #00e0ff, #4facfe);
    background-size: 200% 200%;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    animation: textShine 4s ease-in-out infinite;
    position: relative;
    z-index: 2;
    text-align: center;
    grid-column: 2;
    width: 100%;
}

@keyframes textShine {
    0%, 100% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
}

.header-right {
    display: flex;
    align-items: center;
    gap: 10px;
    position: relative;
    z-index: 2;
    grid-column: 3;
    justify-content: flex-end;
    min-width: 100px;
}

.settings-icon {
    color: #00e0ff;
    font-size: 1.8rem;
    cursor: pointer;
    transition: all 0.4s ease;
    filter: drop-shadow(0 5px 15px rgba(0, 224, 255, 0.4));
    padding: 12px;
    border-radius: 50%;
    background: rgba(0, 224, 255, 0.1);
    border: 1px solid rgba(0, 224, 255, 0.2);
}

.settings-icon:hover {
    transform: scale(1.2) rotate(15deg);
    filter: drop-shadow(0 8px 25px rgba(0, 224, 255, 0.7));
    background: rgba(0, 224, 255, 0.2);
    border-color: #00e0ff;
}

/* Ultra premium game setup screen */
.game-setup {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 75vh;
    padding: 20px 0;
}

.setup-card {
    background: linear-gradient(135deg, rgba(27, 20, 68, 0.95), rgba(15, 12, 41, 0.9));
    border-radius: 35px;
    padding: 40px 30px;
    backdrop-filter: blur(35px);
    border: 1px solid rgba(0, 224, 255, 0.3);
    box-shadow: 
        0 30px 60px rgba(0, 0, 0, 0.5),
        inset 0 1px 0 rgba(255, 255, 255, 0.15);
    max-width: 800px;
    width: 100%;
    box-sizing: border-box;
    text-align: center;
    position: relative;
    overflow: hidden;
    transform-style: preserve-3d;
    margin: 0 15px;
}

.setup-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 5px;
    background: linear-gradient(90deg, #00e0ff, #4facfe, #0099cc);
    border-radius: 35px 35px 0 0;
    box-shadow: 0 0 30px rgba(0, 224, 255, 0.7);
}

.setup-card::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 300px;
    height: 300px;
    background: radial-gradient(circle, rgba(0, 224, 255, 0.08) 0%, transparent 70%);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    animation: setupGlow 12s ease-in-out infinite;
}

@keyframes setupGlow {
    0%, 100% { transform: translate(-50%, -50%) scale(1) rotate(0deg); opacity: 0.5; }
    50% { transform: translate(-50%, -50%) scale(1.2) rotate(180deg); opacity: 0.8; }
}

.setup-card h2 {
    color: white;
    font-size: 3.2rem;
    margin-bottom: 20px;
    font-weight: bold;
    text-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
    position: relative;
    z-index: 2;
}

.setup-card p {
    color: #bbb;
    font-size: 1.4rem;
    margin-bottom: 50px;
    line-height: 1.8;
    position: relative;
    z-index: 2;
}

/* Premium settings grid */
.settings-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 30px;
    margin-bottom: 50px;
    position: relative;
    z-index: 2;
}

.setting-group {
    text-align: left;
}

.setting-group label {
    display: block;
    font-weight: bold;
    color: white;
    margin-bottom: 12px;
    font-size: 1.2rem;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.4);
}

.setting-group select {
    width: 100%;
    padding: 18px 25px;
    background: linear-gradient(135deg, rgba(0, 224, 255, 0.15), rgba(48, 43, 99, 0.25));
    border: 2px solid rgba(0, 224, 255, 0.25);
    border-radius: 20px;
    color: white;
    font-size: 1.1rem;
    cursor: pointer;
    transition: all 0.4s ease;
    backdrop-filter: blur(15px);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

.setting-group select:focus {
    outline: none;
    border-color: #00e0ff;
    background: linear-gradient(135deg, rgba(0, 224, 255, 0.25), rgba(48, 43, 99, 0.35));
    box-shadow: 
        0 12px 35px rgba(0, 224, 255, 0.3),
        0 0 0 3px rgba(0, 224, 255, 0.2);
    transform: translateY(-2px);
}

.setting-group select option {
    background: rgba(27, 20, 68, 0.95);
    color: white;
    padding: 10px;
}

/* Premium difficulty preview */
.difficulty-preview {
    background: linear-gradient(135deg, rgba(0, 224, 255, 0.15), rgba(48, 43, 99, 0.25));
    border-radius: 25px;
    padding: 35px;
    margin-bottom: 50px;
    border: 1px solid rgba(0, 224, 255, 0.25);
    backdrop-filter: blur(15px);
    position: relative;
    z-index: 2;
}

.difficulty-indicator {
    display: inline-block;
    color: white;
    padding: 12px 25px;
    border-radius: 30px;
    font-weight: bold;
    margin-bottom: 18px;
    text-transform: uppercase;
    letter-spacing: 1px;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
    font-size: 1.1rem;
}

.difficulty-preview p {
    margin: 0;
    color: #ddd;
    font-size: 1.2rem;
    line-height: 1.6;
}

/* Ultra premium start game button */
.start-game-button {
    background: linear-gradient(45deg, #00e0ff, #4facfe);
    color: white;
    border: none;
    padding: 25px 60px;
    border-radius: 35px;
    font-size: 1.6rem;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
    display: flex;
    align-items: center;
    gap: 18px;
    margin: 0 auto;
    box-shadow: 
        0 15px 40px rgba(0, 224, 255, 0.5),
        inset 0 1px 0 rgba(255, 255, 255, 0.2);
    text-transform: uppercase;
    letter-spacing: 1.5px;
    position: relative;
    overflow: hidden;
    z-index: 2;
}

.start-game-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    transition: left 0.6s ease;
}

.start-game-button:hover::before {
    left: 100%;
}

.start-game-button:hover {
    background: linear-gradient(45deg, #4facfe, #00bfff);
    transform: translateY(-5px) scale(1.05);
    box-shadow: 
        0 20px 50px rgba(0, 224, 255, 0.7),
        inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

/* Ultra premium loading screen */
.loading-screen {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80vh;
    color: white;
    text-align: center;
    background: linear-gradient(135deg, rgba(15, 12, 41, 0.9), rgba(48, 43, 99, 0.8));
    border-radius: 30px;
    backdrop-filter: blur(30px);
    border: 1px solid rgba(0, 224, 255, 0.3);
    box-shadow: 
        0 25px 50px rgba(0, 0, 0, 0.4),
        inset 0 1px 0 rgba(255, 255, 255, 0.1);
    position: relative;
    overflow: hidden;
}

.loading-screen::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, #00e0ff, #4facfe, #0099cc);
    border-radius: 30px 30px 0 0;
    animation: loadingPulse 2s ease-in-out infinite;
}

@keyframes loadingPulse {
    0%, 100% { box-shadow: 0 0 20px rgba(0, 224, 255, 0.5); }
    50% { box-shadow: 0 0 40px rgba(0, 224, 255, 0.8); }
}

.loading-screen h2 {
    font-size: 2.8rem;
    margin-bottom: 20px;
    background: linear-gradient(45deg, #ffffff, #00e0ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.loading-screen p {
    font-size: 1.4rem;
    color: #bbb;
    line-height: 1.8;
}

/* Premium back button */
.back-button {
    background: linear-gradient(45deg, rgba(0, 224, 255, 0.2), rgba(48, 43, 99, 0.4));
    color: white;
    padding: 10px 18px;
    border-radius: 25px;
    font-weight: bold;
    font-size: 1rem;
    display: flex;
    align-items: center;
    gap: 10px;
    border: 1px solid rgba(0, 224, 255, 0.3);
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
    text-decoration: none;
    grid-column: 1;
    width: fit-content;
    justify-content: center;
    max-width: 150px;
    z-index: 5;
}

.back-button:hover {
    transform: translateY(-3px);
    background: linear-gradient(45deg, rgba(0, 224, 255, 0.3), rgba(48, 43, 99, 0.5));
    border-color: #00e0ff;
    box-shadow: 0 12px 35px rgba(0, 224, 255, 0.4);
}

.score-display {
    background: linear-gradient(45deg, #00e0ff, #4facfe);
    color: white;
    padding: 10px 16px;
    border-radius: 30px;
    font-weight: bold;
    font-size: 1.1rem;
    display: flex;
    align-items: center;
    gap: 8px;
    box-shadow: 
        0 8px 25px rgba(0, 224, 255, 0.5),
        inset 0 1px 0 rgba(255, 255, 255, 0.2);
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
    animation: scoreGlow 3s ease-in-out infinite;
    max-width: 150px;
}

@keyframes scoreGlow {
    0%, 100% { box-shadow: 0 8px 25px rgba(0, 224, 255, 0.5), inset 0 1px 0 rgba(255, 255, 255, 0.2); }
    50% { box-shadow: 0 12px 35px rgba(0, 224, 255, 0.7), inset 0 1px 0 rgba(255, 255, 255, 0.3); }
}

/* Premium streak display */
.streak-display {
    background: linear-gradient(45deg, #ff6b35, #f7931e);
    color: white;
    padding: 15px 25px;
    border-radius: 25px;
    font-weight: bold;
    font-size: 1.1rem;
    display: flex;
    align-items: center;
    gap: 10px;
    box-shadow: 
        0 8px 25px rgba(255, 107, 53, 0.4),
        inset 0 1px 0 rgba(255, 255, 255, 0.2);
    animation: streakPulse 1.5s ease-in-out infinite;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.streak-display.fire {
    background: linear-gradient(45deg, #e74c3c, #ff6b35);
    animation: fireGlow 1s ease-in-out infinite;
}

@keyframes streakPulse {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.05); }
}

@keyframes fireGlow {
    0%, 100% { 
        box-shadow: 0 8px 25px rgba(231, 76, 60, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.2);
        transform: scale(1);
    }
    50% { 
        box-shadow: 0 12px 35px rgba(231, 76, 60, 0.7), inset 0 1px 0 rgba(255, 255, 255, 0.3);
        transform: scale(1.08);
    }
}

/* Celebration overlay */
.celebration-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    animation: celebrationFadeIn 0.5s ease-out;
}

@keyframes celebrationFadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

.celebration-content {
    background: linear-gradient(135deg, rgba(255, 107, 53, 0.95), rgba(247, 147, 30, 0.95));
    padding: 60px 50px;
    border-radius: 30px;
    text-align: center;
    color: white;
    backdrop-filter: blur(20px);
    border: 2px solid rgba(255, 255, 255, 0.3);
    box-shadow: 
        0 30px 60px rgba(0, 0, 0, 0.5),
        inset 0 1px 0 rgba(255, 255, 255, 0.2);
    animation: celebrationBounce 0.6s ease-out;
}

@keyframes celebrationBounce {
    0% { transform: scale(0.3) rotate(-10deg); opacity: 0; }
    50% { transform: scale(1.1) rotate(5deg); }
    100% { transform: scale(1) rotate(0deg); opacity: 1; }
}

.celebration-icon {
    font-size: 4rem;
    margin-bottom: 20px;
    color: #ffd700;
    filter: drop-shadow(0 5px 15px rgba(255, 215, 0, 0.5));
    animation: crownRotate 2s ease-in-out infinite;
}

@keyframes crownRotate {
    0%, 100% { transform: rotateY(0deg) scale(1); }
    50% { transform: rotateY(180deg) scale(1.1); }
}

.celebration-content h2 {
    font-size: 2.5rem;
    margin-bottom: 15px;
    text-shadow: 0 3px 6px rgba(0, 0, 0, 0.4);
}

.celebration-content p {
    font-size: 1.4rem;
    margin-bottom: 25px;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.celebration-sparkles {
    display: flex;
    justify-content: center;
    gap: 20px;
}

.celebration-sparkles span {
    font-size: 2rem;
    animation: sparkleFloat 2s ease-in-out infinite;
}

.celebration-sparkles span:nth-child(1) { animation-delay: 0s; }
.celebration-sparkles span:nth-child(2) { animation-delay: 0.2s; }
.celebration-sparkles span:nth-child(3) { animation-delay: 0.4s; }
.celebration-sparkles span:nth-child(4) { animation-delay: 0.6s; }
.celebration-sparkles span:nth-child(5) { animation-delay: 0.8s; }

@keyframes sparkleFloat {
    0%, 100% { transform: translateY(0px) rotate(0deg) scale(1); }
    50% { transform: translateY(-20px) rotate(180deg) scale(1.2); }
}

/* Best streak display */
.best-streak-display {
    background: linear-gradient(45deg, #9b59b6, #8e44ad);
    color: white;
    padding: 12px 20px;
    border-radius: 20px;
    font-weight: bold;
    font-size: 1rem;
    display: flex;
    align-items: center;
    gap: 8px;
    box-shadow: 
        0 6px 20px rgba(155, 89, 182, 0.4),
        inset 0 1px 0 rgba(255, 255, 255, 0.2);
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.best-streak-display svg {
    color: #ffd700;
    filter: drop-shadow(0 2px 4px rgba(255, 215, 0, 0.5));
}

/* Button glow effect */
.button-glow {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(45deg, rgba(255, 255, 255, 0.2), transparent);
    border-radius: 35px;
    animation: buttonGlowMove 2s ease-in-out infinite;
    pointer-events: none;
}

@keyframes buttonGlowMove {
    0%, 100% { opacity: 0; transform: translateX(-100%) skewX(-15deg); }
    50% { opacity: 1; transform: translateX(100%) skewX(-15deg); }
}

/* Ultra premium progress section */
.game-progress {
    margin-bottom: 50px;
    text-align: center;
    background: linear-gradient(135deg, rgba(0, 224, 255, 0.15), rgba(48, 43, 99, 0.25));
    padding: 30px;
    border-radius: 25px;
    border: 1px solid rgba(0, 224, 255, 0.25);
    backdrop-filter: blur(15px);
    box-shadow: 
        0 15px 35px rgba(0, 0, 0, 0.3),
        inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.progress-bar {
    width: 100%;
    height: 15px;
    background: rgba(0, 224, 255, 0.2);
    border-radius: 15px;
    overflow: hidden;
    margin-bottom: 20px;
    box-shadow: inset 0 2px 5px rgba(0, 0, 0, 0.3);
}

.progress-fill {
    height: 100%;
    background: linear-gradient(45deg, #00e0ff, #4facfe);
    border-radius: 15px;
    transition: width 0.6s cubic-bezier(0.4, 0, 0.2, 1);
    box-shadow: 
        0 2px 10px rgba(0, 224, 255, 0.5),
        inset 0 1px 0 rgba(255, 255, 255, 0.3);
    position: relative;
    overflow: hidden;
}

.progress-fill::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    animation: progressShine 2s ease-in-out infinite;
}

@keyframes progressShine {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(100%); }
}

.progress-text {
    color: white;
    font-size: 1.3rem;
    font-weight: 600;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.4);
}

/* Ultra premium card container */
.card-container {
    display: flex;
    justify-content: center;
    margin-top: 30px;
    margin-bottom: 50px;
    perspective: 1200px;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    padding: 0 15px;
    position: relative;
    z-index: 1;
}

.flash-card {
    width: 100%;
    max-width: 500px;
    height: 380px;
    position: relative;
    cursor: pointer;
    transform-style: preserve-3d;
    transition: transform 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.flash-card.flipped {
    transform: rotateY(180deg);
}

.card-front,
.card-back {
    width: 100%;
    height: 100%;
    position: absolute;
    backface-visibility: hidden;
    border-radius: 30px;
    padding: 45px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 
        0 20px 50px rgba(0, 0, 0, 0.5),
        inset 0 1px 0 rgba(255, 255, 255, 0.15);
    border: 1px solid rgba(0, 224, 255, 0.3);
    backdrop-filter: blur(25px);
    overflow: hidden;
}

.card-front {
    background: linear-gradient(135deg, rgba(27, 20, 68, 0.95), rgba(15, 12, 41, 0.9));
    color: white;
}

.card-back {
    background: linear-gradient(135deg, rgba(0, 224, 255, 0.95), rgba(79, 172, 254, 0.9));
    color: white;
    transform: rotateY(180deg);
}

.card-front::before,
.card-back::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, #00e0ff, #4facfe, #0099cc);
    border-radius: 30px 30px 0 0;
    box-shadow: 0 0 20px rgba(0, 224, 255, 0.6);
}

.card-category {
    background: rgba(0, 224, 255, 0.25);
    padding: 12px 25px;
    border-radius: 30px;
    font-size: 1rem;
    font-weight: 600;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 1px;
    border: 1px solid rgba(0, 224, 255, 0.4);
    color: #00e0ff;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.card-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 30px 0;
}

.card-content h3 {
    font-size: 1.8rem;
    margin-bottom: 30px;
    font-weight: bold;
    text-shadow: 0 3px 6px rgba(0, 0, 0, 0.4);
    color: #00e0ff;
}

.card-content p {
    font-size: 1.5rem;
    line-height: 1.7;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.difficulty-badge {
    padding: 8px 18px;
    border-radius: 15px;
    font-size: 0.9rem;
    font-weight: 600;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 1px;
    border: 1px solid rgba(0, 224, 255, 0.3);
    color: #00e0ff;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.difficulty-badge.facile {
    background: linear-gradient(45deg, #4caf50, #66bb6a);
    color: white;
    border-color: rgba(76, 175, 80, 0.5);
}

.difficulty-badge.moyen {
    background: linear-gradient(45deg, #ff9800, #ffb74d);
    color: white;
    border-color: rgba(255, 152, 0, 0.5);
}

.difficulty-badge.difficile {
    background: linear-gradient(45deg, #f44336, #ef5350);
    color: white;
    border-color: rgba(244, 67, 54, 0.5);
}

.flip-hint {
    text-align: center;
    font-size: 1.1rem;
    color: #bbb;
    font-style: italic;
    margin-top: 20px;
    animation: hintPulse 3s ease-in-out infinite;
}

@keyframes hintPulse {
    0%, 100% { opacity: 0.6; }
    50% { opacity: 1; }
}

/* Ultra premium answer buttons */
.answer-buttons {
    display: flex;
    justify-content: center;
    gap: 40px;
    margin-bottom: 40px;
}

.answer-button {
    padding: 20px 40px;
    border: none;
    border-radius: 35px;
    font-size: 1.3rem;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    text-transform: uppercase;
    letter-spacing: 1px;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
    position: relative;
    overflow: hidden;
}

.answer-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    transition: left 0.6s ease;
}

.answer-button:hover::before {
    left: 100%;
}

.answer-button.correct {
    background: linear-gradient(45deg, #4caf50, #66bb6a);
    color: white;
    border: 2px solid rgba(76, 175, 80, 0.5);
}

.answer-button.wrong {
    background: linear-gradient(45deg, #f44336, #ef5350);
    color: white;
    border: 2px solid rgba(244, 67, 54, 0.5);
}

.answer-button:hover {
    transform: translateY(-5px) scale(1.05);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.4);
}

.answer-button.correct:hover {
    box-shadow: 0 15px 40px rgba(76, 175, 80, 0.5);
}

.answer-button.wrong:hover {
    box-shadow: 0 15px 40px rgba(244, 67, 54, 0.5);
}

/* Ultra premium game results */
.game-results {
    text-align: center;
    background: linear-gradient(135deg, rgba(27, 20, 68, 0.95), rgba(15, 12, 41, 0.9));
    border-radius: 35px;
    padding: 60px 50px;
    backdrop-filter: blur(35px);
    border: 1px solid rgba(0, 224, 255, 0.3);
    box-shadow: 
        0 30px 60px rgba(0, 0, 0, 0.5),
        inset 0 1px 0 rgba(255, 255, 255, 0.15);
    position: relative;
    overflow: hidden;
}

.game-results::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 5px;
    background: linear-gradient(90deg, #00e0ff, #4facfe, #0099cc);
    border-radius: 35px 35px 0 0;
    box-shadow: 0 0 30px rgba(0, 224, 255, 0.7);
}

.game-results h2 {
    font-size: 3.5rem;
    margin-bottom: 30px;
    background: linear-gradient(45deg, #ffffff, #00e0ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.final-score {
    font-size: 4rem;
    font-weight: bold;
    color: #00e0ff;
    margin: 30px 0;
    text-shadow: 0 5px 15px rgba(0, 224, 255, 0.5);
    animation: scoreReveal 1s ease-out;
}

@keyframes scoreReveal {
    0% { transform: scale(0); opacity: 0; }
    50% { transform: scale(1.2); }
    100% { transform: scale(1); opacity: 1; }
}

.results-stats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 25px;
    margin: 40px 0;
}

.stat-item {
    background: linear-gradient(135deg, rgba(0, 224, 255, 0.15), rgba(48, 43, 99, 0.25));
    padding: 25px 20px;
    border-radius: 20px;
    border: 1px solid rgba(0, 224, 255, 0.25);
    backdrop-filter: blur(15px);
    transition: all 0.3s ease;
}

.stat-item:hover {
    transform: translateY(-5px);
    background: linear-gradient(135deg, rgba(0, 224, 255, 0.25), rgba(48, 43, 99, 0.35));
    border-color: #00e0ff;
    box-shadow: 0 15px 35px rgba(0, 224, 255, 0.2);
}

.stat-item .stat-label {
    font-weight: 600;
    color: #bbb;
    font-size: 1.1rem;
    margin-bottom: 8px;
}

.stat-item .stat-value {
    font-weight: bold;
    font-size: 1.6rem;
    color: #00e0ff;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.performance-message {
    font-size: 1.4rem;
    color: #ddd;
    margin-bottom: 40px;
    font-weight: 600;
    line-height: 1.7;
    padding: 20px;
    background: rgba(0, 224, 255, 0.1);
    border-radius: 20px;
    border: 1px solid rgba(0, 224, 255, 0.2);
}

.results-actions {
    display: flex;
    gap: 25px;
    justify-content: center;
    flex-wrap: wrap;
}

.restart-button,
.home-button {
    padding: 20px 40px;
    border: none;
    border-radius: 35px;
    font-size: 1.2rem;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    text-transform: uppercase;
    letter-spacing: 1px;
    position: relative;
    overflow: hidden;
}

.restart-button::before,
.home-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    transition: left 0.6s ease;
}

.restart-button:hover::before,
.home-button:hover::before {
    left: 100%;
}

.restart-button {
    background: linear-gradient(45deg, #00e0ff, #4facfe);
    color: white;
    box-shadow: 0 10px 30px rgba(0, 224, 255, 0.4);
    display: flex;
    align-items: center;
    gap: 12px;
}

.home-button {
    background: linear-gradient(45deg, rgba(0, 224, 255, 0.2), rgba(48, 43, 99, 0.4));
    color: #00e0ff;
    border: 2px solid rgba(0, 224, 255, 0.3);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

.restart-button:hover,
.home-button:hover {
    transform: translateY(-3px) scale(1.05);
}

.restart-button:hover {
    box-shadow: 0 15px 40px rgba(0, 224, 255, 0.6);
}

.home-button:hover {
    background: linear-gradient(45deg, rgba(0, 224, 255, 0.3), rgba(48, 43, 99, 0.5));
    border-color: #00e0ff;
    box-shadow: 0 12px 35px rgba(0, 224, 255, 0.4);
}

/* Game stats styling to prevent overlap with cards */
.game-stats {
    display: flex;
    justify-content: center;
    gap: 30px;
    margin: 10px auto 25px auto;
    padding: 15px 30px;
    background: linear-gradient(135deg, rgba(15, 12, 41, 0.8), rgba(27, 20, 68, 0.7));
    border-radius: 20px;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(0, 224, 255, 0.2);
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
    max-width: 500px;
    animation: fadeIn 0.5s ease-out;
    position: relative;
    z-index: 5;
}

.stat {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 20px;
    border-radius: 15px;
    transition: all 0.3s ease;
    background: rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    min-width: 140px;
}

.stat-label {
    font-size: 1rem;
    opacity: 0.9;
    margin-bottom: 8px;
    font-weight: 500;
    color: #ddd;
}

.stat-value {
    font-size: 1.8rem;
    font-weight: bold;
}

.stat-value.correct {
    color: #4CAF50;
    text-shadow: 0 0 10px rgba(76, 175, 80, 0.5);
}

.stat-value.incorrect {
    color: #F44336;
    text-shadow: 0 0 10px rgba(244, 67, 54, 0.5);
}

/* Responsive design */
@media (max-width: 768px) {
    .flash-cards-container {
        padding: 10px;
        min-height: calc(100vh - 20px); /* Adjust for smaller padding */
    }
      .flash-cards-header {
        grid-template-columns: 1fr;
        grid-template-rows: auto auto auto;
        padding: 20px 15px;
        margin-bottom: 20px;
        gap: 15px;
        text-align: center;
    }
    
    .flash-cards-header h1 {
        font-size: 1.8rem;
        order: 2;
        grid-row: 2;
        grid-column: 1;
    }
    
    .back-button {
        order: 1;
        grid-row: 1;
        grid-column: 1;
        margin: 0 auto;
    }
      .header-right {
        justify-content: center;
        order: 3;
        grid-row: 3;
        grid-column: 1;
    }
    
    /* Responsive game stats for mobile */
    .game-stats {
        flex-direction: row;
        padding: 12px 20px;
        max-width: 90%;
        gap: 15px;
    }
    
    .stat {
        padding: 8px 15px;
        min-width: 110px;
    }
    
    .stat-value {
        font-size: 1.5rem;
    }
    
    .setup-card {
        padding: 25px 20px;
        border-radius: 25px;
    }
    
    .settings-grid {
        grid-template-columns: 1fr;
    }
    
    .flash-card {
        height: 320px;
    }
    
    .card-front, .card-back {
        padding: 25px;
    }
    
    .game-controls {
        flex-direction: column;
        gap: 15px;
        align-items: center;
    }
    
    .game-controls button {
        width: 100%;
        max-width: 200px;
    }
    
    .game-results {
        padding: 30px 20px;
    }
}

@media (max-width: 480px) {
    .flash-cards-header h1 {
        font-size: 1.4rem;
    }
    
    .flash-cards-header {
        padding: 15px 12px;
    }
    
    .score-display {
        padding: 10px 15px;
        font-size: 1rem;
    }
    
    .streak-display,
    .best-streak-display {
        padding: 8px 15px;
        font-size: 0.9rem;
    }
    
    .setup-card {
        padding: 20px 15px;
    }
    
    .setting-group label {
        font-size: 0.9rem;
    }
    
    .start-game-button {
        font-size: 1rem;
        padding: 12px 20px;
    }
    
    .flash-card {
        height: 300px;
    }
    
    .card-question {
        font-size: 1.3rem;
    }
    
    .card-answer {
        font-size: 1.8rem;
    }
    
    .action-button {
        padding: 8px 15px;
        font-size: 0.9rem;
    }
    
    .back-button {
        padding: 8px 15px;
        font-size: 0.9rem;
        min-width: 80px;
    }
}

/* Additional media queries for specific viewport sizes */
@media (min-width: 481px) and (max-width: 615px) {
    .flash-cards-header {
        flex-direction: column;
    }
    
    .back-button {
        align-self: flex-start;
        margin-bottom: 10px;
    }
    
    .flash-cards-header h1 {
        margin-bottom: 10px;
    }
}

@media (min-width: 616px) and (max-width: 900px) {
    .flash-cards-header h1 {
        font-size: 1.8rem;
    }
    
    .back-button {
        padding: 10px 15px;
    }
    
    .score-display, .streak-display, .best-streak-display {
        padding: 10px 15px;
        font-size: 0.95rem;
    }
}
